.Burger {
  width: 100%;
  height: 300px;
  margin: auto;
  overflow: auto;
  text-align: center;
  font-weight: bold;
  font-size: 1.2em;
}

@media (min-width: 500px) and (max-height: 400px) {
  .Burger {
    width: 350px;
    height: 320px;
  }
}

@media (min-width: 400px) and (min-height: 401px) {
  .Burger {
    width: 300px;
    height: 250px;
  }
}

@media (min-width: 1000px) and (min-height: 700px) {
  .Burger {
    width: 700px;
    height: 600px;
  }
}


